<template>
<div>
    <el-select v-model="choice" @change="changeDate">
        <el-option v-for="item in date" :key="item.way" :label="item.label" :value="item.way">
        </el-option>
    </el-select>
    <div class="commodity_loading" v-show="isLoading"></div>
    <div v-show="!isLoading" class="commodity_table_wrapper">
        <el-table class="commodity_table" border :data="commoditySales" cell-class-name="commodity_cell" header-row-class-name="commodity_head" style="width: 100%" height="89%">
            <el-table-column :label="$t('data.salesRanking')" width="width">
                <template slot-scope="scope">
                    {{ scope.$index + 1 }}
                </template>
            </el-table-column>
            <el-table-column prop="name" :label="$t('data.commodityName')" width="width">
            </el-table-column>
            <el-table-column prop="sales" :label="$t('data.sales')" width="width">
            </el-table-column>
        </el-table>
    </div>
</div>
</template>

<script>
import {
    mapActions,
    mapState
} from 'vuex';
import {
    startLoading,
    endLoading
} from '@/util/loading.js';
export default {
    mounted() {
        startLoading('.commodity_loading');
        let obj = {
            way: this.choice,
            shop_id: this.shopId,
        };
        this.getProductStatistics(obj).then(() => {
            this.isLoading = false;
            endLoading();
        });
    },
    data() {
        return {
            date: [{
                    way: 'day',
                    label: this.$t('data.today'),
                },
                {
                    way: 'week',
                    label: this.$t('data.week'),
                },
                {
                    way: 'month',
                    label: this.$t('data.month'),
                },
            ],
            choice: 'day',
            isLoading: true,
        };
    },
    methods: {
        ...mapActions({
            getProductStatistics: 'statistics/getProductStatistics',
        }),
        changeDate(value) {
            startLoading('.commodity_loading');
            this.isLoading = true;

            let obj = {
                way: value,
                shop_id: this.shopId,
            };
            this.getProductStatistics(obj).then(() => {
                this.isLoading = false;
                endLoading();
            });
        },
    },
    computed: {
        ...mapState({
            shopId: (state) => state.user.data.role_id,
            commoditySales: (state) => state.statistics.commoditySales,
        }),
    },
};
</script>

<style lang="scss" scoped>
.commodity_table_wrapper {
    height: calc(100vh - 200px);
}

.commodity_loading {
    padding-top: 4em;
    height: 200px;
}

.commodity_table {
    margin-top: 1em;
}

::v-deep .commodity_cell {
    text-align: center;
}

::v-deep .commodity_head {
    font-weight: bold;
    color: $--color-primary;

    .cell {
        text-align: center;
    }
}
</style>
